import * as THREE from 'three';
import { RendererThree, MercatorProjection, } from '../../../../lib';
import { getJSONFromURL, getColorByCelsius } from 'njscore';

import { FilterGLViewDarkForDarken } from '../01_JSAPI/Tint/FilterGLViewDarkForDarken';

export class Solution extends RendererThree {

    streetOSM: any = [];
    routesTreated: any = [];
    routesControl: any = [];
    zones30: any = [];

    meshStreet: THREE.LineSegments;
    meshSpike: THREE.LineSegments;

    raycaster = new THREE.Raycaster();
    pointer = new THREE.Vector2();
    sphere: THREE.Mesh;

    constructor(id: string = 'main') {
        super(id)

        const light = new THREE.PointLight(0xffffff, 0.8);
        this.camera.add(light);
        this.scene.add(light);

        // const canvasGL = document.createElement('canvas');
        // canvasGL.style.position = 'absolute';
        // canvasGL.style.pointerEvents = 'none';
        // canvasGL.style.mixBlendMode = 'darken';
        // canvasGL.width = this.hostDiv.clientWidth;
        // canvasGL.height = this.hostDiv.clientHeight;
        // this.hostDiv.appendChild(canvasGL);
        // const glCtx = canvasGL.getContext('webgl');
        // const d = new FilterGLViewDarkForDarken(glCtx, 0.5, 5)
        // d.Render(glCtx);


        this.raycaster = new THREE.Raycaster();
        this.raycaster.params.Line.threshold = 3;
        this.raycaster.far = 1000;

        const sphereGeometry = new THREE.SphereGeometry(0.2, 32, 32);
        const sphereMaterial = new THREE.MeshBasicMaterial({ color: 0xffffff });
        const sphere = new THREE.Mesh(sphereGeometry, sphereMaterial);
        this.sphere = sphere;
        this.scene.add(sphere);

        this.camera.position.set(0, 20, 20);

        // const path = '/static/research/slow-zone/';
        // getJSONFromURL(process.env.PUBLIC_URL + path + 'street_osm.geojson').then((data) => {
        getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/street_osm.geojson').then((data) => {
            for (let i = 0; i < data['features'].length; ++i) {
                this.streetOSM.push(data['features'][i]['geometry']['coordinates']);
            }
            this.meshStreet = renderLines(this.scene, this.streetOSM, [0, 1, 1]);
            // renderPoints(this.scene, this.streetOSM, [0, 1, 1]);


            getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/treated-st.geojson').then((data) => {
                for (let i = 0; i < data['features'].length; ++i) {
                    this.routesTreated.push(data['features'][i]['geometry']['coordinates']);
                }
                // renderLines(this.scene, this.routesTreated, [1, 0, 0]);
                this.meshSpike = renderLinesSpike(this.scene, this.routesTreated, [1, 0, 0]);
            });

            getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/control-st.geojson').then((data) => {
                for (let i = 0; i < data['features'].length; ++i) {
                    this.routesControl.push(data['features'][i]['geometry']['coordinates']);
                }
                renderLines(this.scene, this.routesControl, [0, 1, 0]);
            });



            this.start();
        });
    }

    public update(scene: THREE.Scene) {
        // console.log('dddd');
        this.raycaster.setFromCamera(this.pointer, this.camera);
        const intersects = this.raycaster.intersectObjects([this.meshStreet, this.meshSpike], true);

        if (intersects.length > 0) {
            this.sphere.visible = true;
            this.sphere.position.copy(intersects[0].point);
        } else {

            this.sphere.visible = false;
        }


        for (let i = 1; i < this.meshSpike.geometry.attributes.position.count; i += 2) {
            let p = this.meshSpike.geometry.attributes.position;

            let x = p.getX(i);
            let y = p.getY(i);
            let z = p.getZ(i)

            y = Math.sin(5 * x * z + this.t * 5) * 9

            y = y < 0 ? 0 : y;

            let np = new THREE.Vector3(x, y, z);
            this.meshSpike.geometry.attributes.position.setXYZ(i, np.x, np.y, np.z);


            const col = getColorByCelsius(y * 40)
            // console.log(col)

            // this.meshSpike.geometry.attributes.color.setXYZ(i -1, col[0] /255, col[1] /255, col[2] /255);
            // this.meshSpike.geometry.attributes.color.setXYZ(i,    col[0] /255, col[1] /255, col[2] /255);

            this.meshSpike.geometry.attributes.color.setXYZ(i - 1, 0, y, 0);
            this.meshSpike.geometry.attributes.color.setXYZ(i, y, 0, 0);
        }

        this.meshSpike.geometry.attributes.position.needsUpdate = true;
        this.meshSpike.geometry.attributes.color.needsUpdate = true;
        this.meshSpike.geometry.computeVertexNormals();


    }
    public mouseMove(e: MouseEvent): void {
        // console.log(e)
        this.pointer.x = ((e.offsetX + 2) / this.hostDiv.clientWidth) * 2 - 1;
        this.pointer.y = - ((e.offsetY - 70) / this.hostDiv.clientHeight) * 2 + 1;
    }
}


const renderPoints = (scene: THREE.Scene, paths: any, color: number[]) => {
    const geometry = new THREE.BufferGeometry();
    const material = new THREE.PointsMaterial({ vertexColors: true, size: 0.5 });

    const positions = [];
    const colors = [];


    const xOFf = -15478803.88731685;
    const yOFf = -4766681.448690384;
    const scale = 0.01;


    for (let i = 0; i < paths.length; ++i) {
        const route = paths[i]

        for (let j = 0; j < route.length; ++j) {
            for (let k = 0; k < route[j].length; ++k) {

                const [xPro, yPro] = MercatorProjection(route[j][k][0], route[j][k][1]);

                const x = (xPro + xOFf) * scale
                const y = (yPro + yOFf) * scale

                positions.push(x, 0, y)

                colors.push(color[0], color[1], color[2]);


            }
        }
    }

    geometry.setAttribute('position', new THREE.Float32BufferAttribute(positions, 3));
    geometry.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));
    // generateMorphTargets( geometry );

    geometry.computeBoundingSphere();

    const line = new THREE.Points(geometry, material);
    scene.add(line);


}
const renderLinesSpike = (scene: THREE.Scene, paths: any, color: number[]) => {
    const geometry = new THREE.BufferGeometry();
    // const material = new THREE.PointsMaterial( { vertexColors: true, size: 0.1 } );
    const material = new THREE.LineBasicMaterial({ vertexColors: true });

    const positions = [];
    const colors = [];
    const indices = [];

    const xOFf = -15478803.88731685;
    const yOFf = -4766681.448690384;
    const scale = 0.01;


    let xC = 0
    let yC = 0
    let Count = 0

    for (let i = 0; i < paths.length; ++i) {
        const route = paths[i]

        for (let j = 0; j < route.length; ++j) {
            for (let k = 0; k < route[j].length; ++k) {

                const [xPro, yPro] = MercatorProjection(route[j][k][0], route[j][k][1]);
                xC += xPro;
                yC += yPro;
                Count += 1;

                const x = (xPro + xOFf) * scale
                const y = (yPro + yOFf) * scale
                // console.log(x, y)
                positions.push(x, 0, y)
                positions.push(x, 10, y)

                colors.push(color[0], color[1], color[2]);
                colors.push(color[0], color[1], color[2]);

                // if (k < route[j].length - 1) {
                //     indices.push(Math.floor(positions.length / 3) - 1, Math.floor(positions.length / 3));
                //     indices.push(Math.floor(positions.length / 3) - 1, Math.floor(positions.length / 3));
                // }
            }
        }
    }
    //     console.log(xC / Count, yC / Count)
    // geometry.setIndex(indices);
    geometry.setAttribute('position', new THREE.Float32BufferAttribute(positions, 3));
    geometry.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));
    // generateMorphTargets( geometry );

    geometry.computeBoundingSphere();

    const line = new THREE.LineSegments(geometry, material);
    scene.add(line);
    return line
};


const renderLines = (scene: THREE.Scene, paths: any, color: number[]) => {

    const geometry = new THREE.BufferGeometry();
    // const material = new THREE.PointsMaterial( { vertexColors: true, size: 0.1 } );
    const material = new THREE.LineBasicMaterial({ vertexColors: true });

    const positions = [];
    const colors = [];
    const indices = [];

    const xOFf = -15478803.88731685;
    const yOFf = -4766681.448690384;
    const scale = 0.01;


    let xC = 0
    let yC = 0
    let Count = 0

    for (let i = 0; i < paths.length; ++i) {
        const route = paths[i]

        for (let j = 0; j < route.length; ++j) {
            for (let k = 0; k < route[j].length; ++k) {

                const [xPro, yPro] = MercatorProjection(route[j][k][0], route[j][k][1]);
                xC += xPro;
                yC += yPro;
                Count += 1;

                const x = (xPro + xOFf) * scale
                const y = (yPro + yOFf) * scale
                // console.log(x, y)
                positions.push(x, 0, y)

                colors.push(color[0], color[1], color[2]);

                if (k < route[j].length - 1) {
                    indices.push(Math.floor(positions.length / 3) - 1, Math.floor(positions.length / 3));
                }
            }
        }
    }
    //     console.log(xC / Count, yC / Count)
    geometry.setIndex(indices);
    geometry.setAttribute('position', new THREE.Float32BufferAttribute(positions, 3));
    geometry.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));
    // generateMorphTargets( geometry );

    geometry.computeBoundingSphere();

    const line = new THREE.LineSegments(geometry, material);
    scene.add(line);
    return line
};